<template> 
  <div class="body-div">
    <div class="top-div">
      <h1>{{selectedGroupName}}</h1>
      <button v-if="isLoopMode" class="loop end" @click="endLoop()">🟥</button>
      <button v-else class="loop start" @click="startLoop()">▶</button>
    </div>
    <div v-for="device in devices" :key="device.id" class="device-div">
    <p class="icon good" v-if="device.status" style="color:green">✔</p>
    <p class="icon bad" v-else >❌</p>
    <p>{{ device.name }}</p>
    <p>{{ device.ip }}</p>
    </div>
  </div>
</template>
  <script>


  export default {
      props:["devices","selectedGroupName"], 
      name:"BodyComp",
      data(){
          return{
            isLoopMode : false,
            interval : null,
          }
      },
      methods:{
        endLoop(){
          this.isLoopMode = ! this.isLoopMode
          clearInterval(this.interval)
        },
        startLoop(){
          this.isLoopMode = ! this.isLoopMode
          this.interval = setInterval(this.start,9000)
        },
        start(){
          this.$emit("get-devices-loop")
        }
      },
     beforeMount(){
      },
      mounted(){ 
      }
  }
  </script>
  
