<template> 
    <div class="body-div">
      <div class="top-div">
        <h1>{{selectedGroupName}}</h1>
        <button v-if="isLoopMode" class="loop end" @click="endLoop()">🟥</button>
        <button v-else class="loop start" @click="startLoop()">▶</button>
      </div>
      <div v-for="group in groups" :key="group.id" class="device-div">
        <p class="icon good" v-if="group.tkulim == 0" style="color:green">✔</p>
        <p class="icon bad" v-else >❌</p>
        <p>{{ group.name }}</p>
        <p> תקולים: {{ group.tkulim }} </p>
      </div>
      <!-- <div v-for="device in devices" :key="device.id" class="device-div">
      <p class="icon good" v-if="device.status" style="color:green">✔</p>
      <p class="icon bad" v-else >❌</p>
      <p>{{ device.name }}</p>
      <p>{{ device.ip }}</p>
      </div> -->
    </div>
  </template>
    <script>
  
  import axios from "axios"

    export default {
        props:["devices","selectedGroupName", "url"], 
        name:"HomeBodyComp",
        data(){
            return{
              isLoopMode : false,
              interval : null,
              groups:[],
            }
        },
        methods:{
          endLoop(){
            this.isLoopMode = ! this.isLoopMode
            clearInterval(this.interval)
          },
          startLoop(){
            this.isLoopMode = ! this.isLoopMode
            this.interval = setInterval(this.start,9000)
          },
          async start(){
            console.log('loop')
            var response = await axios.get(this.$props['url']+'api/groupsHomePing/')
            this.groups = response.data
            // this.$emit("get-devices-loop")
          },
          async getGroups(){
            var response = await axios.get(this.$props['url']+'api/groupsHome/')
            this.groups = response.data
          }
        },
       beforeMount(){
        this.getGroups()
        },
        mounted(){ 
        }
    }
    </script>
    
    <style scoped>
    .top-div{
      background-color: rgb(151, 199, 221);
      width: 100%;
      height: 100px;
      display: flex;
      justify-content: center;
      align-items: center;
      box-shadow:  0  0 10px rgb(0,0,0,.25);
    }
    .body-div{
      width: 100%;
      background-color: rgb(224, 220, 220);
      display: flex;
      flex-direction: row;
      flex-flow: row wrap;
      align-items: flex-start;
      align-content: flex-start;
      overflow-y: scroll;
    }
    .device-div{
      background-color: rgb(247, 247, 247);
      width:160px;
      height: 160px;
      display: flex;
      flex-direction: column;
      box-shadow: 20px;
      box-shadow:  0  0 10px rgb(0,0,0,.25);
  
    }
    .icon{
      font-size: 35px;
      margin:  0;
    }
    .good{
      background-color: rgba(159, 226, 159, 0.5);
    }
    .bad{
      background-color: rgba(226, 159, 159, 0.5);
    }
    p{
      font-size: 18px;
    }
    .loop{
      width: 50px;
      height: 50px;
      background-color: transparent;
      outline: none;
      border: none;
    }
    .start{
      font-size: 40px;
      color:rgb(6, 189, 6);
  
    }
    .end{
      font-size: 25px;
  
    }
    </style>