<template> 
  <div class="right-div">
    <div class="icon-div" @click="getDevices(-1)">
      <img src="../assets/logo.png"/>
      <h3>CA</h3>
    </div>
    <div v-for="group in groups" :key="group.id"  :class="[group.id == selectedGroup ? 'groupDdivSelected':'', 'groupDdiv']"  @click="getDevices(group.id , group.name)">
      {{group.name }}
    </div>
  </div>
</template>
  <script>
  import axios from "axios"

  export default {
      props:[], 
      name:"RightComp",
      data(){
          return{
            groups : [],
            url: 'http://127.0.0.1:8000/',
            selectedGroup : -1,
          }
      },
      methods:{
       async getGroups(){
        var response = await axios.get(this.url+'api/group/list/')
        this.groups = response.data
        },
      getDevices(groupId, groupName){
        this.selectedGroup = groupId
        this.$emit("get-devices" , groupId, groupName)
      },
      },
     beforeMount(){
       
      this.getGroups()


      },
      mounted(){ 
        console.log(this.groups)
      }
  }
  </script>
  
